import React, { useState, useEffect, memo } from "react";
import {
  ZoomableGroup,
  ComposableMap,
  Geographies,
  Geography,
  Sphere,
  Graticule
} from "react-simple-maps";

import axios from 'axios';

// import { scaleLinear } from "d3-scale";

const geoUrl =
  "https://raw.githubusercontent.com/zcreativelabs/react-simple-maps/master/topojson-maps/world-110m.json";

// const colorScale = d3.scaleLinear()
//   .domain([0, 50, 100, 500, 1000, 5000, 10000, 50000, 100000, 500000, 1000000, 5000000, 10000000])
//   .range(["#FFFDD0", "#7FFF00", "#FFFF00", "#F4C430", "#FFBF00", "#FF7F00", "#FF7F00", "#FF0000", "#FF2400", "#800000", "#65000B", "#660000", "#000000"]);

const numberWithCommas = (x) => {
  x = x.toString();
  var pattern = /(-?\d+)(\d{3})/;
  while (pattern.test(x))
      x = x.replace(pattern, "$1,$2");
  return x;
}

const MapToolTip = ({ setTooltipContent }) => {
  const [data, setData] = useState([]);

  useEffect(() => {
    axios.get('https://api.covid19api.com/summary').then((response) => {
      console.log(response);
      setData(response.data.Countries)
    });
  }, []);

  return (
    <React.Fragment>
      <ComposableMap style={{width:"100%", padding:"0"}} data-tip="COVID-19 Total Cases" projectionConfig={{ scale: 130 }}>
        <Sphere stroke="#9EB9D4" strokeWidth={1} />
        <Graticule stroke="#9EB9D4" strokeWidth={1} />
        {data.length > 0 && (
          <Geographies geography={geoUrl}>
            {({ geographies }) =>
              geographies.map(geo => {
                const d = data.find((s) => s.CountryCode === geo.properties.ISO_A2);
                return (
                  <Geography
                    key={geo.rsmKey}
                    geography={geo}
                    onMouseEnter={() => {
                      const { NAME, POP_EST } = geo.properties;
                      setTooltipContent(`${d ? d["Country"] : NAME} —  ${d ? 'Total Cases: ' + numberWithCommas(d["TotalConfirmed"]) : 'No Case Recorded'}`);
                    }}
                    onMouseLeave={() => {
                      setTooltipContent("");
                    }}
                    style={{
                      default: {
                        fill: "#555D50",
                        outline: "none"
                      },
                      hover: {
                        fill: "#58111A",
                        outline: "none"
                      },
                      pressed: {
                        fill: "#C80815",
                        outline: "none"
                      }
                    }}
                    // fill={d ? colorScale(d["TotalConfirmed"]) : "#5D8AA8"}
                  />
                )
              })
            }
          </Geographies>

        )}
      </ComposableMap>
    </React.Fragment>
  );
};

export default memo(MapToolTip);



